<template>
  <div class="vx-settings">
    <vx-header :showhome="1" :title="$t('common.settings')" @home="$router.push({ name: 'index' })"></vx-header>
    <div class="vx-container">
      <div class="setting_cont">
        <vx-cell :title="$t('settings.enterConfig')" @link="$router.push({name: 'enterconfig'})"></vx-cell>
        <vx-cell :title="$t('settings.LED')" @link="$router.push({ name: 'led' })"></vx-cell>
      </div>
      <div class="setting_cont" v-if="!getIsVcan6S">
        <vx-cell :title="$t('settings.modeSwitch')" :value="mode==1 ? $t('settings.SplicerMode') : $t('settings.SwitcherMode')" @link="$router.push({name: 'selmode'})"></vx-cell>
      </div>
      <div class="setting_cont">
        <!-- 场景特效 -->
        <!-- <vx-cell :title="$t('settings.sceneEffects')" :value="scene.title" @link="$router.push({name: 'sceneeffects'})"></vx-cell> -->
        <vx-cell :title="$t('settings.takeEffects')" :value="effect.title" @link="$router.push({name: 'takeeffects'})">
        </vx-cell>
      </div>
      <div class="setting_cont">
        <vx-cell :title="$t('settings.language')" :value="selLanguage" @link="$router.push({name: 'languages'})"></vx-cell>
        <vx-cell :title="$t('settings.checkForUpdates')" :showBadge="getNewVersion===0?true:false" @link="$router.push({name: 'update'})"></vx-cell>
        <vx-cell :title="$t('settings.updatePassword')" @link="$router.push({name: 'password'})"></vx-cell>
        <vx-cell :title="$t('settings.help')" @link="$router.push({name: 'help'})"></vx-cell>
      </div>
    </div>
  </div>
</template>

<script>
  import htp from "../../api/http";
  import urls from "../../api/apiurl";
  import { getLoc } from 'utils';
  import { mapGetters } from 'vuex';
  export default {
    data() {
      return {
        ctrlMode: false,
        showSceneEffects: false,
        showLanguage: false,
        selLanguage: '',
        mode: 1,
        scene: {
          title: ''
        },
        effect: {}
      };
    },
    computed: {
      ...mapGetters(['getIsVcan6S', 'getNewVersion'])
    },
    created() {
      this.selLanguage = getLoc('lang') === 'zh' ? this.$t('settings.chinese') : this.$t('settings.english');
      this.mode = getLoc('setWorkModel');
    },
    mounted() {

      this.getTakeSpecialEfficiency();
    },
    methods: {
      getTakeSpecialEfficiency() {
        htp.get(urls.getTakeSpecialEfficiency).then(response => {
          if(response.status === 10000000) {
            let effect = response.data;
            switch(effect.specialEfficiencyId) {
              case 0:   //直切vx6
                effect.title = this.$t('settings.cut');
                effect.icon = 'cut';
                break;
              case 1:   //淡入淡出vx6
                effect.title = this.$t('settings.fade');
                effect.icon = 'fade';
                break;
              case 2:   //左上擦除
                effect.title = this.$t('settings.wipeTopleft');
                effect.icon = 'wipe-top-left';
                break;
              case 3:   //自左擦除
                effect.title = this.$t('settings.wipeLeft');
                effect.icon = 'wipe-left';
                break;
              case 4:   //左下擦除
                effect.title = this.$t('settings.wipeBottomLeft');
                effect.icon = 'wipe-bottom-left';
                break;
              case 5:   //自底擦除
                effect.title = this.$t('settings.wipeBottom');
                effect.icon = 'wipe-bottom';
                break;
              case 6:   //右下擦除
                effect.title = this.$t('settings.wipeBottomRight');
                effect.icon = 'wipe-bottom-right';
                break;
              case 7:   //自右擦除
                effect.title = this.$t('settings.wipeRight');
                effect.icon = 'wipe-right';
                break;
              case 8:   //右上擦除
                effect.title = this.$t('settings.wipeTopRight');
                effect.icon = 'wipe-top-right';
                break;
              case 9:   //自顶擦除
                effect.title = this.$t('settings.wipeTop');
                effect.icon = 'wipe-top';
                break;
              case 11:   //上下展开
                effect.title = this.$t('settings.splitHorizontallyOut');
                effect.icon = 'split-horizontally-out';
                break;
              case 10:   //左右展开
                effect.title = this.$t('settings.splitVerticallyOut');
                effect.icon = 'split-vertically-out';
                break;
              case 12:   //由内向外
                effect.title = this.$t('settings.zoomIn');
                effect.icon = 'zoom-in';
                break;
              case 13:   //左上滑入vx6
                effect.title = this.$t('settings.coverTopLeft');
                effect.icon = 'cover-top-left';
                break;
              case 14:   //自左滑入vx6
                effect.title = this.$t('settings.coverLeft');
                effect.icon = 'cover-left';
                break;
              case 15:   //左下滑入vx6
                effect.title = this.$t('settings.coverBottomLeft');
                effect.icon = 'cover-bottom-left';
                break;
              case 16:   //自底滑入vx6
                effect.title = this.$t('settings.coverBottom');
                effect.icon = 'cover-bottom';
                break;
              case 17:   //右下滑入vx6
                effect.title = this.$t('settings.coverBottomRight');
                effect.icon = 'cover-bottom-right';
                break;
              case 18:   //自右滑入vx6
                effect.title = this.$t('settings.coverRight');
                effect.icon = 'cover-right';
                break;
              case 19:   //右上滑入vx6
                effect.title = this.$t('settings.coverTopRight');
                effect.icon = 'cover-top-right';
                break;
              case 20:   //自顶滑入vx6
                effect.title = this.$t('settings.coverTop');
                effect.icon = 'cover-top';
                break;
              case 21:   //上下收缩
                effect.title = this.$t('settings.splitHorizontallyIn');
                effect.icon = 'split-horizontally-in';
                break;
              case 22:   //左右收缩
                effect.title = this.$t('settings.splitVerticallyIn');
                effect.icon = 'split-vertically-in';
                break;
              case 23:   //由外向内
                effect.title = this.$t('settings.zoomOut');
                effect.icon = 'zoom-out';
                break;
              case 24:   //由内向外（矩形）
                effect.title = this.$t('settings.zoomInRectangle');
                effect.icon = 'zoom-in-rectangle';
                break;
              case 25:   //由外向内（矩形）
                effect.title = this.$t('settings.zoomOutRectangle');
                effect.icon = 'zoom-out-rectangle';
                break;
              case 28:   //由内向外（菱形）
                effect.title = this.$t('settings.zoomInDiamond');
                effect.icon = 'zoom-in-diamond';
                break;
              case 29:   //由内向外（菱形）
                effect.title = this.$t('settings.zoomOutDiamond');
                effect.icon = 'zoom-out-diamond';
                break;
              case 26:   //由内向外（圆）
                effect.title = this.$t('settings.zoomInCircular');
                effect.icon = 'zoom-in-circular';
                break;
              case 27:   //由外向内（圆）
                effect.title = this.$t('settings.zoomOutCircular');
                effect.icon = 'zoom-out-circular';
                break;
              default:
                effect.title = this.$t('settings.without');
                effect.icon = 'without';
                break;
            }
            this.effect = effect;
          }
        });
      }
    }
  };
</script>

<style lang="less" scoped>
  .vx-settings {
    background: #212124;
    color: #a8a8aa;
  }
  .setting_cont {
    margin-top: 25px;
    .vc-cell {
      border-top: 1px solid #4f5054; /*no*/
      &:first-child {
        border-top: none;
      }
    }

    .vx-progress {
      border-bottom: 1px #4f5054 solid; /*no*/
      background-color: #383940;
      padding-top: 20px;
      padding-bottom: 30px;
    }
    .progress-list {
      text-align: left;
      font-size: 24px;
      height: 60px;
      line-height: 60px;
      text-indent: 35px;
      .vx-progress {
        margin-top: 0;
        color: #a8a8aa;
      }
    }
  }
</style>




